// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
import update from 'react-addons-update'; // ES6
import {FaRegCircle, FaTimes} from 'react-icons/fa'

const AppComponent = styled.div`
	@import url('https://fonts.googleapis.com/css?family=Sniglet:400,800&display=swap&subset=latin-ext');
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
	font-family: 'Sniglet', sans-serif;
	justify-content: center;
	align-items: center;
	font-size: 20px;
`
const Round = styled.div`
	position: absolute;
	top: 30px;
	left: 30px;
`
const Turn = styled.div`
	position: absolute;
	top: 30px;
	right: 30px;
`
const Icon = styled.span`
	font-size: 25px;
	color: var(--color-decorative);
`
const Cross = styled.div``
const Line = styled.div`
	position: absolute;
	border-radius: 20px;
	:nth-child(1),
	:nth-child(2) {
		top: 50%;
		transform: translate(-50%, -50%);
		width: 5vmin;
		max-width: 20px;
		background-color: var(--color-main);
		height: 90vmin;
	}
	:nth-child(3),
	:nth-child(4) {
		left: 50%;
		transform: translate(-50%, -50%);
		width: 90vmin;
		background-color: var(--color-main);
		height: 5vmin;
		max-height: 20px;
	}
	:nth-child(1) {
		left: calc(50% - 15vmin);
	}
	:nth-child(2) {
		left: calc(50% + 15vmin);
	}
	:nth-child(3) {
		top: calc(50% - 15vmin);
	}
	:nth-child(4) {
		top: calc(50% + 15vmin);
	}
	@media (min-width: 700px) {
		:nth-child(1),
		:nth-child(2) {
			height: 60vmin;
		}
		:nth-child(3),
		:nth-child(4) {
			width: 60vmin;
		}
		:nth-child(1) {
			left: calc(50% - 10vmin);
		}
		:nth-child(2) {
			left: calc(50% + 10vmin);
		}
		:nth-child(3) {
			top: calc(50% - 10vmin);
		}
		:nth-child(4) {
			top: calc(50% + 10vmin);
		}
	}
`
const Cells = styled.div`
	width: 90vmin;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: repeat(3, 30vmin);
	cursor: pointer;
	@media (min-width: 700px) {
		width: 60vmin;
		grid-template-rows: repeat(3, 20vmin);
	}
	-webkit-tap-highlight-color: transparent;
`
const show = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9)
	}
	to {
		opacity: 1;
		transform: scale(1)
	}
`
const Cell = styled.div`
	display: none;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 60px;
	opacity: 0;
	transition: opacity 0.2s ease;
	/* animation: ${show} 0.3s ease; */
	/* :hover {
		opacity: 0.9;
		::after {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			${props =>
				props.circle &&
				css`
					content: 'o';
				`
			}
			${props =>
				props.cross &&
				css`
					content: 'x';
				`
			}
		}
	} */
`
const Winner = styled.div`
	position: absolute;
	bottom: 30px;
	text-align: center;
`
const WinnerIcon = styled.div`
	font-size: 25px;
	color: var(--color-decorative);
	font-size: 40px;
	margin: 0 10px;
`

class App extends Component {
	state = {
		round: 1,
		board: [
			['', '', ''],
			['', '', ''],
			['', '', ''],
		],
		winner: null,
	}
	pick = (e) => {
		console.log('winner: ', this.state.winner);
		if (!this.state.winner) {
			const turn = this.state.round % 2 !== 0 ? "player1" : "player2";
			console.log(turn);
			const element = document.getElementById(e.target.id);
			if (!element.innerHTML) {
				const {row, column} = e.target.dataset;
				console.log(row, column);
				this.state.round < 9 ? this.setState({round: this.state.round + 1}) : this.setState({winner: 'lost'})
				this.state.board[row][column] = turn;
				this.forceUpdate();
				console.log(this.state.board);
				this.check();
				this.state.round % 2 === 0 ? element.innerHTML="o" : element.innerHTML="x"
				element.style.animation = `show 0.3s ease`;
				element.style.opacity = 1;
				element.style.cursor = "default";
			}
		}
		else {
			document.querySelectorAll('.box').forEach((box) => box.style.cursor = 'default')
		}
	}
	check = () => {
		const combinations = [
			// poziomo
			[0,1,2], [3,4,5], [6,7,8],
			// pionowo
			[0,3,6], [1,4,7], [2,5,8],
			// na skos
			[0,4,8], [2,4,6]
		] 
		const result = this.state.board.reduce((total, row) => total.concat(row));
		let moves = {
			'player1': [],
			'player2': []
		}
		result.forEach((field, index) => moves[field] ? moves[field].push(index) : null);
		combinations.forEach(combination => {
			if (combination.every(index => moves['player1'].indexOf(index) > -1)) {
				this.setState({winner: 'player1'})
			}
			else if (combination.every(index => moves['player2'].indexOf(index) > -1)) {
				this.setState({winner: 'player2'})
			}
		})
	}
	render() {
		const board = this.state.board;
		return (
			<AppComponent>
				<Round>Runda: {this.state.winner ? <Icon>-</Icon> : <Icon>{this.state.round}</Icon>}</Round>
				<Turn>Ruch: {this.state.winner ? <Icon>-</Icon> : this.state.round % 2 === 0 ? <Icon>o</Icon> : <Icon>x</Icon>}</Turn>
				<Cross>
					<Line />
					<Line />
					<Line />
					<Line />
				</Cross>
				<Cells>
					{board.map((row, rowIndex) => {
						return (
							row.map((column, columnIndex) => {
								return (
									<Cell
										id={`r${rowIndex}c${columnIndex}`}
										circle={this.state.round % 2 === 0 ? true : false}
										cross={this.state.round % 2 !== 0 ? true : false}
										className="box"
										onClick={this.pick}
										data-row={rowIndex}
										data-column={columnIndex}
									/>
								)
							})
						)
					})}
				</Cells>
				
				{this.state.winner === 'player1' ? (
					<Winner>
						Wygrywa:
						<WinnerIcon>x</WinnerIcon>
					</Winner>
				) : this.state.winner === 'player2' ? (
					<Winner>
						Wygrywa:
						<WinnerIcon>o</WinnerIcon>
					</Winner>
				) : this.state.winner === 'lost' ? (
					<Winner>
						Remis!
						<WinnerIcon>o x</WinnerIcon>
					</Winner>
				) : ''}
				
			</AppComponent>
		);
	}
}

export default App